.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@media only screen and (max-width: 600px) {
img.card-img-top {
  width: 100px;
  border-radius: 50%;
  height: 100px;
}
.hmt h1 {
    font-size: 1rem!important;
    text-align: center !important;
    font-family: 'Merienda', cursive;
    padding: 50px 10px!important;
    position: relative;
}
p.hmtext {
  padding: 0 30px !important;
}
}
body{background-color: #222;font-family: 'Pompiere', cursive;font-size: 24px;}
.side, .card{background-color: #000;Color:#fff;}
.side ul li a {
  list-style: none;
  text-decoration: none !important;
  color: #fff;
  font-size: 20px;
}
.side ul li a:hover{color:#0d6efd;}
.side ul li {
  list-style: none;
  text-decoration: none !important;
  border-bottom: 1px solid #222;
  padding: 4px 0;
}
.side ul {
  margin: 0;
  padding: 0;
}
.side ul li:hover {
  list-style: none;
  text-decoration: none !important;
  color: #fff;
  border-bottom: 1px solid #0d6efd;
  padding: 4px 0;
}
.progress {
  display: flex;
  height: 0.5rem;
}
.icon {
  width: 150px;
  margin: auto;
}
.hmt{margin:0px;padding:0px;}
.hmt h1 {
  font-size: 4.5rem;
  text-align: left;
  font-family: 'Merienda', cursive;
  padding: 50px 100px;
  position: relative;
}
.hmt h1:before {
  content: '<h1>';
  font-size: 20px;
  top: 28px;
  position: absolute;
  color: #515152;
  font-style: italic;
}
.hmt h1:after {
  content: '</h1>';
  font-size: 20px;
  bottom: 28px;
  position: absolute;
  color: #515152;
  font-style: italic;
}
p.hmtext:before, .hmtex:before {
  content: '<p>';
  position: relative;
  font-size: 20px;
  top: -25px;
  color: #515152;
  font-style: italic;
}
p.hmtext:after, .hmtex:after {
  content: '</p>';
  position: relative;
  font-size: 20px;
  bottom: -25px;
  color: #515152;
  font-style: italic;
}
svg:hover {
  color: #0d6efd;
}

svg {
  margin: 0 10px;
}
.card-header:first-child {
  border-bottom: 1px solid #222;
}
.spar{padding:0 30px 0 0px}
.mar{margin:0px;}
p.hmtext {
  padding: 0 100px;
  text-align: left;
  text-indent: -22px;
}
p.hmtex {
  padding: 0 30px;
  text-align: left;
}
.hmt {
  background-image: url(http://drive.google.com/uc?export=view&id=1fom0sFsiIuzENYWBkt1f2idvC_tdQIH3);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: bottom;
}
.hmt .card {
  background: rgba(0,0,0,.7) !important;
  margin:0px !important;
}

.html {
    text-align: left;
    padding: 0 20px;
}
.html:before{
  content: '<html>';
  position: relative;
  font-size: 20px;
  top: 0px;
  color: #515152;
  font-style: italic;
}
.html:after {
  content: '<body>';
  position: relative;
  font-size: 20px;
  top: 20px;
  color: #515152;
  font-style: italic;
  left: -20px;
}